import * as React from 'react';
import { MenuItem } from '@ali/we-design';
import { PluginRoles } from '@ali/4ever-bamboo';
import { MenuItemInnerProps } from '@ali/4ever-component';
import { decreaseIndent } from '../actions';

export default function DecreaseIndentMenu(props: MenuItemInnerProps) {
  const { menuItem, locale, controller } = props;
  const { title, ...rest } = menuItem;

  const handleClick = () => controller.run('onAction', decreaseIndent());

  return (
    <MenuItem {...rest} onClick={handleClick} role={PluginRoles.indentDecrease}>
      {title || locale?.locale?.leftButtonTitle}
    </MenuItem>
  );
}
